<template>
    <div>
        <div class="header">
            <van-icon @click="routeBack" class="left-icon" name="arrow-left" />
            <van-search
                class="search"
                v-model="value"
                show-action
                placeholder="请输入搜索关键词"
                shape="round"
                @search="onSearch"
                @clear="onCancel"
            >
                <template #action>
                    <div @click="onSearch">搜索</div>
                </template>
            </van-search>
        </div>
        <div class="word" v-if="searchResult.length == 0">
            <div class="title">搜索热词</div>
            <ul class="words">
                <li
                    v-for="(item, index) in currentHotWords()"
                    :key="index"
                    @click="submitWord(item.word)"
                >
                    {{ item.word }}
                </li>
            </ul>
        </div>
        <div class="word" v-if="searchResult.length == 0">
            <div class="title">
                <span>搜索历史</span
                ><span
                    ><van-icon
                        @click="delHistory"
                        v-if="historyWords.length != 0"
                        name="delete"
                /></span>
            </div>
            <div class="empty" v-if="historyWords.length == 0">
                暂无历史搜索记录
            </div>
            <ul class="words" v-else>
                <li
                    v-for="(item, index) in historyWords"
                    :key="index"
                    @click="submitWord(item)"
                >
                    {{ item }}
                </li>
            </ul>
        </div>
        <div class="hot-books" v-if="searchResult.length == 0">
            <div class="title">全站热搜</div>
            <ul class="books">
                <li
                    v-for="(item, index) in hotBooks"
                    :key="index"
                    :class="{ hot: index < 3 }"
                    @click="routeTo(item.book)"
                >
                    <span class="item-text">{{ item.word }}</span>
                    <van-icon name="arrow" />
                </li>
            </ul>
        </div>
        <ul class="search-result" v-if="searchResult.length != 0">
            <BookCard
                v-for="(item, index) in searchResult"
                :key="index"
                :item="item"
            ></BookCard>
        </ul>
    </div>
</template>

<script>
import { getData } from "@/api/api.js";
import store from "@/store/index.js";
import BookCard from "@/components/BookCard.vue";
export default {
    name: "Search",
    components: {
        BookCard,
    },
    data() {
        return {
            value: "",
            hotWords: [],
            hotBooks: [],
            historyWords: [],
            searchResult: [],
        };
    },
    methods: {
        routeBack() {
            this.$router.back();
        },
        onSearch() {
            let historyWords = JSON.parse(
                localStorage.getItem("history-words")
            );

            if (historyWords) {
                if (historyWords.data.indexOf(this.value) == -1) {
                    historyWords.data.push(this.value);
                    this.historyWords.push(this.value);
                }
                localStorage.setItem(
                    "history-words",
                    JSON.stringify({
                        data: historyWords.data,
                    })
                );
            } else {
                let data = [];
                data.push(this.value);
                this.historyWords.push(this.value);
                localStorage.setItem(
                    "history-words",
                    JSON.stringify({
                        data: data,
                    })
                );
            }
            store.state.loading = true;

            getData("http://novel.kele8.cn/search", {
                keyword: this.value,
            })
                .then((res) => {
                    this.searchResult = res.data.books;
                    store.state.loading = false;
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        },
        onCancel() {
            this.searchResult = [];
        },
        currentHotWords() {
            let newArr = [];
            for (let index = 0; index < this.hotWords.length; index++) {
                if (newArr.length < 10) {
                    newArr.push(this.hotWords[index]);
                } else {
                    break;
                }
            }
            return newArr;
        },
        submitWord(word) {
            this.value = word;
            this.onSearch();
        },
        delHistory() {
            this.historyWords = [];
            let historyWords = JSON.parse(
                localStorage.getItem("history-words")
            );

            historyWords.data = [];
            localStorage.setItem("history-words", JSON.stringify(historyWords));
        },
        routeTo(id) {
            this.$router.push({ name: "BookDetail", params: { id: id } });
        },
    },
    beforeRouteEnter(to, from, next) {
        let hotWords = [];
        let hotBooks = [];
        let historyWords = [];

        let hotWordsData = JSON.parse(localStorage.getItem("hot-words"));

        if (hotWordsData && hotWordsData.expires > Date.now()) {
            hotWords = hotWordsData.data.searchHotWords;
        } else {
            store.state.loading = true;
            getData("http://novel.kele8.cn/search-hotwords")
                .then((res) => {
                    localStorage.setItem(
                        "hot-words",
                        JSON.stringify({
                            expires: Date.now() + 24 * 60 * 60 * 1000,
                            data: res.data,
                        })
                    );
                    hotWords = res.data.searchHotWords;
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        }

        let hotBooksData = JSON.parse(localStorage.getItem("hot-books"));

        if (hotBooksData && hotBooksData.expires > Date.now()) {
            hotBooks = hotBooksData.data.newHotWords;
        } else {
            store.state.loading = true;
            getData("http://novel.kele8.cn/hot-books")
                .then((res) => {
                    localStorage.setItem(
                        "hot-books",
                        JSON.stringify({
                            expires: Date.now() + 24 * 60 * 60 * 1000,
                            data: res.data,
                        })
                    );
                    hotBooks = res.data.newHotWords;
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        }

        let historyWordsData = JSON.parse(
            localStorage.getItem("history-words")
        );

        if (historyWordsData) {
            historyWords = historyWordsData.data;
        }

        next((vm) => {
            vm.hotWords = hotWords;
            vm.hotBooks = hotBooks;
            vm.historyWords = historyWords;
        });
    },
};
</script>

<style lang="less" scoped>
.header {
    display: flex;
    align-items: center;
    .left-icon {
        flex: 1;
    }
    .search {
        flex: 11;
    }
}
.word {
    margin-top: 10px;
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #eee;
    .title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        color: #999;
    }
    .empty {
        padding: 10px;
        text-align: center;
        font-size: 13px;
        color: #e4393c;
    }
    .words {
        display: flex;
        flex-wrap: wrap;
        padding: 5px;
        li {
            font-size: 13px;
            margin: 15px 0 0 10px;
            background-color: #f4f4f4;
            padding: 5px 7px;
            border-radius: 40px;
        }
    }
}
.hot-books {
    margin-top: 10px;
    padding: 10px;
    text-align: left;
    .title {
        font-size: 14px;
    }
    .books {
        padding: 5px;
        li {
            padding: 5px;
            margin-top: 5px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .hot {
            color: #e4393c;
        }
    }
}
</style>
